.GoBackLink {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: auto;
  max-width: 1240px;
  height: 50px;
  margin: 0 auto;
  padding: 0px 20px;
  position: relative;
  margin-bottom: -50px;
  z-index: 2;
  a {
    font-weight: 500;
    text-transform: uppercase;
    color: #fff;
    transition: 0.3s;
    &:hover,
    &:focus {
      color: #3ec58e;
    }
  }
}
